import axios from 'axios'

require('es6-promise').polyfill()

const apiServerProtocol = 'http://'
const apiServerHost = '52.79.193.215:8080/v1'

function autocomplete (value) {
  const path = '/autocomplete'
  return axios.get(
    `${apiServerProtocol}${apiServerHost}${path}?text=${encodeURI(value)}`
  )
}

export default {
  autocomplete
}



