<template>
  <div class="searchInfo">
    <div @click="$emit('toggle-search-info')">
      <div>{{ inputData.destination }}</div>
      <div>
        <span class="roboto">{{ dateFormat(inputData.checkin) }}</span>({{ getDayOfWeek(inputData.checkin) }})
        <span> - </span>
        <span class="roboto">{{ dateFormat(inputData.checkout) }}</span>({{ getDayOfWeek(inputData.checkout) }})
        <span> / {{ getBetweenDay(inputData.checkin, inputData.checkout) }}박</span>
        <span>{{ getTotalPassenger }}</span>
      </div>
      <button></button>
    </div>
    <div class="drop-shadow-down" v-bind:class="viewIndex === 0 ? '' : 'show'"></div>
  </div>
</template>

<script>
export default {
  props: ['inputData', 'viewIndex'],
  data () {
    return {
      textOfMonth: ['01', '02', '03', '04', '05', '06', '07', '08', '09', '10', '11', '12'],
      dayOfWeek: ['일', '월', '화', '수', '목', '금', '토']
    }
  },
  computed: {
    getChildrenNumber () {
      return this.inputData.children.length
    },
    getTotalPassenger () {
      return parseInt(this.inputData.adults) + this.getChildrenNumber
    }
  },
  methods: {
    dateFormat (date) {
      const newDate = new Date(date)
      return `${this.textOfMonth[newDate.getMonth()]}.${this.zeros(newDate.getDate())}`
    },
    getDayOfWeek (date) {
      const newDate = new Date(date)
      return this.dayOfWeek[newDate.getDay()]
    },
    getBetweenDay (sdate, edate) {
      const strStart = `${sdate.toString().substr(0, 4)}-${sdate.toString().substr(5, 2)}-${sdate.toString().substr(8, 2)}`
      const strEnd = `${edate.toString().substr(0, 4)}-${edate.toString().substr(5, 2)}-${edate.toString().substr(8, 2)}`
      function getDateObj (strDate) {
        const dateArray = strDate.split('-')
        return new Date(dateArray[0], Number(dateArray[1]) - 1, dateArray[2])
      }
      return (getDateObj(strEnd).getTime() - getDateObj(strStart).getTime()) / 86400000
    },
    zeros (n) {
      let zero = ''
      let newN = n
      newN = n.toString()
      if (newN.length < 2) {
        for (let i = 0; i < 2 - newN.length; i += 1) {
          zero += '0'
        }
      }
      return zero + newN
    }
  }
}
</script>

<style scoped>
  .searchInfo{
    color: #FFF;
    background-color: #8F9D9D;
    width: 100%;
    z-index: 2;
    height: 70px;
    position: relative;
  }
  .searchInfo > div:first-of-type{
    background-color: #8F9D9D;
    padding: 17px 0 0;
    height: 70px;
    text-align: center;
    width: 100%;
  }
  .searchInfo > div button{
    border-radius: 37px;
    padding: 0;
    height: 50px;
    width: 50px;
    border: 0;
    position: absolute;
    top: 10px;
    right: 18px;
    background: url(../../assets/search-51.svg) no-repeat;
  }
  .searchInfo > div > div:first-of-type{
    line-height: 19px;
    height: 19px;
    font-size: 16px;
    padding: 0 68px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }
  .searchInfo > div > div:last-of-type{
    line-height: 1.21;
    font-size: 14px;
    margin-top: 4px;
  }
  .drop-shadow-down {
    width: 100%;
    position: absolute;
    left: 0;
    bottom: -33px;
    height: 33px;
    opacity: 0;
    background-image: linear-gradient(to bottom, #1a1a1c, rgba(26, 26, 28, 0.48) 45%, rgba(26, 26, 28, 0) 98%);
    z-index: 1;
    transition: opacity .5s ease;
    pointer-events: none;
  }
  .drop-shadow-down.show{
    opacity: .25;
  }
</style>



