<template>
  <div>
    <div v-if="loading===true" style="padding: 30px; text-align: center">loading</div>
    <div v-else>
      <h1>디테일</h1>
      <div>
        {{ hotelInfo.name }}의 디테일
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailWrapper',
  props: ['hotelList'],
  data () {
    return {
      hotelInfo: {},
      loading: true
    }
  },
  methods: {
    getHotelInfo () {
      setTimeout(() => {
        this.hotelInfo = {
          name: `호텔${this.$route.params.hotelKey}`,
          address: '두바이, 국제금융센터',
          star: 3,
          rating: '9.0',
          review: 2546,
          image: [
            'http://www.socuripass.com/wp-content/uploads/2016/02/vie-budapest-onedaytour-6.jpg',
            'http://www.thebling.co.kr/wp-content/uploads/2017/08/budapest-1500x1039.png'
          ]
        }
        this.loading = false
      }, 1000)
    }
  },
  mounted () {
    if (this.hotelList.list[this.$route.params.hotelKey]) {
      this.hotelInfo = this.hotelList.list[this.$route.params.hotelKey]
      this.loading = false
    } else {
      this.getHotelInfo()
    }
  }
}
</script>

<style scoped>

</style>



