<template>
  <div class="listWrapper">
    <search-info
      :inputData="inputData"
      :viewIndex="hotelIndex"
      @toggle-search-info="openPop('search')"
    ></search-info>
    <div class="hotelList"
      ref="hotelList"
      @touchstart="presentTouchStart"
      @touchend="presentTouchEnd"
      @touchmove="presentTouchMove"
    >
      <div v-if="hotelList.list.length > 0">
        <div class="hotelListCont before"
          v-if="hotelIndex > 0"
          v-bind:style="getContStyle('before')"
          ref="before"
        >
          <hotel-info
            v-bind:key="hotelIndex - 1"
            :index="hotelIndex - 1"
            :hotelInfo="hotelList.list[hotelIndex - 1]"
          ></hotel-info>
        </div>
        <div class="hotelListCont present"
          v-bind:style="getContStyle('present')"
          ref="present"
        >
          <hotel-info
            v-bind:key="hotelIndex"
            :index="hotelIndex"
            :hotelInfo="hotelList.list[hotelIndex]"
            :inputData="inputData"
            :listHeight="listHeight"
            :scrollTop="scrollTop"
            :isPresent="true"
            @set-client-height="setClientHeight"
          ></hotel-info>
        </div>
        <div class="hotelListCont after"
          v-if="hotelIndex < hotelList.list.length - 1"
          v-bind:style="getContStyle('after')"
          ref="after"
        >
          <hotel-info
            v-bind:key="hotelIndex + 1"
            :index="hotelIndex + 1"
            :hotelInfo="hotelList.list[hotelIndex + 1]"
          ></hotel-info>
        </div>
      </div>
      <div v-else style="font-size: 20px; padding: 50px; text-align: center; color: #FFF">loading</div>
    </div>
    <div class="drop-shadow-up" v-bind:class="hotelIndex === hotelList.list.length - 1 ? '' : 'show'"></div>
    <div class="btnContainer">
      <button @click="openPop('filter')">상세조건</button>
      <button>정렬</button>
      <button>지도보기</button>
    </div>
    <search-popup
      v-show="$route.params.popFlag"
      :inputData="inputData"
      :popping="popping"
      :todayString="todayString"
      @open-pop="openPop"
      @close-pop="closePop"
    ></search-popup>
  </div>
</template>

<script>
import SearchPopup from '../Search/SearchPopup'
import HotelInfo from './HotelInfo'
import SearchInfo from './SearchInfo'

export default {
  components: {
    SearchPopup,
    HotelInfo,
    SearchInfo
  },
  props: ['hotelList', 'hotelIndex'],
  data () {
    const inputData = {
      destination: this.$route.params.destination,
      checkin: this.$route.params.checkin,
      checkout: this.$route.params.checkout,
      adults: parseInt(this.$route.params.adults, 10),
      room: parseInt(this.$route.params.room, 10)
    }
    const param = JSON.stringify(inputData)
    inputData.children = []
    if (this.$route.params.children !== 'null') {
      const arrChildren = this.$route.params.children.split('&')
      for (let x = 0; x < arrChildren.length; x += 1) {
        inputData.children.push(parseInt(arrChildren[x], 10))
      }
    }
    return {
      inputData,
      param,
      openSearchContainer: false,
      popping: false,
      presentScale: 1,
      startOffsetY: 0,
      moveOffsetY: 0,
      listHeight: 0,
      scrollGap: 50,
      duringScroll: false,
      filterInfo: {
        type: [],
        star: [],
        price: {
          min: 0,
          max: null
        },
        addition: []
      },
      todayString: new Date().toISOString().split('T')[0],
      isPossibleTop: true,
      isPossibleBottom: true,
      lastTouchOffsetY: 0,
      clientHeight: 0,
      scrollTop: 0,
      scrollTopStatic: 0
    }
  },
  methods: {
    setClientHeight (value) {
      this.scrollTop = 0
      this.scrollTopStatic = 0
      this.clientHeight = value
      this.isPossibleTop = true
      if (this.clientHeight > this.listHeight) {
        this.isPossibleBottom = false
      } else {
        this.isPossibleBottom = true
      }
    },
    presentTouchStart (event) {
      if (this.duringScroll || (!this.isPossibleTop && !this.isPossibleBottom)) {
        return
      }
      this.startOffsetY = event.changedTouches[0].pageY
    },
    presentTouchMove (event) {
      const offsetY = event.changedTouches[0].pageY
      this.lastTouchOffsetY = offsetY
      if (this.duringScroll) {
        return
      }
      if ((!this.isPossibleBottom && this.startOffsetY > offsetY) ||
        (!this.isPossibleTop && this.startOffsetY < offsetY)) {
        let positionY = this.scrollTopStatic + this.startOffsetY - offsetY
        if (positionY < 0) {
          positionY = 0
        } else if (positionY > this.clientHeight - this.listHeight) {
          positionY = this.clientHeight - this.listHeight
        }
        this.scrollTop = positionY
        if (this.scrollTop <= 0) {
          this.isPossibleTop = true
          this.isPossibleBottom = false
        } else if (this.scrollTop + this.listHeight < this.clientHeight) {
          this.isPossibleTop = false
          this.isPossibleBottom = false
        } else {
          this.isPossibleBottom = true
          this.isPossibleTop = false
          this.startOffsetY = this.lastTouchOffsetY
        }
      } else {
        event.preventDefault()
        this.moveOffsetY = offsetY
      }
    },
    presentTouchEnd (event) {
      this.scrollTopStatic = this.scrollTop
      if (this.moveOffsetY === 0 || this.duringScroll) {
        return
      }
      if (!this.isPossibleTop && !this.isPossibleBottom) {
        return
      }
      const gap = this.startOffsetY - this.moveOffsetY
      if (gap > this.scrollGap && this.hotelIndex < this.hotelList.list.length - 1) {
        event.preventDefault()
        this.duringScroll = true
        this.scrollDown(this.$refs.after, 0, 200)
      } else if (gap < -this.scrollGap && this.hotelIndex > 0) {
        event.preventDefault()
        this.duringScroll = true
        this.scrollUp(this.$refs.present, this.listHeight, 200)
      } else {
        this.startOffsetY = 0
        this.moveOffsetY = 0
      }
    },
    getHotelList () {
      setTimeout(() => {
        const arr = [{
          name: '호텔1',
          address: '두바이, 국제금융센터',
          star: 4,
          rating: '9.0',
          review: 2546,
          image: [
            'http://www.socuripass.com/wp-content/uploads/2016/02/vie-budapest-onedaytour-6.jpg',
            'https://gotrip.s3.amazonaws.com/storage/go_trip/product/id/7/1/3/1/b/6/1/7131b61c225ecfb6a2ef65e57094449f/10002/9043f08df49faa258cb413d9e2154c5e.jpg'
          ],
          deeplink: [
            {
              agent: 'agoda',
              price: 321000
            },
            {
              agent: 'booking',
              price: 322000
            }
          ]
        }, {
          name: '호텔2',
          address: '두바이, 국제금융센터',
          rating: '9.0',
          review: 2546,
          image: [
            'https://gotrip.s3.amazonaws.com/storage/go_trip/product/id/7/1/3/1/b/6/1/7131b61c225ecfb6a2ef65e57094449f/10002/9043f08df49faa258cb413d9e2154c5e.jpg'
          ],
          deeplink: [
            {
              agent: 'expedia',
              price: 321000
            }
          ]
        }, {
          name: '호텔3',
          address: '두바이, 국제금융센터',
          star: 5,
          rating: '9.0',
          review: 2546,
          image: [
            'http://www.thebling.co.kr/wp-content/uploads/2017/08/budapest-1500x1039.png'
          ],
          deeplink: [
            {
              agent: 'agoda',
              price: 321000
            },
            {
              agent: 'booking',
              price: 322000
            },
            {
              agent: 'expedia',
              price: 323000
            }
          ]
        }, {
          name: '호텔4',
          address: '두바이, 국제금융센터',
          image: [
            'http://www.socuripass.com/wp-content/uploads/2016/02/vie-budapest-onedaytour-6.jpg'
          ],
          deeplink: [
            {
              agent: 'agoda',
              price: 321000
            },
            {
              agent: 'booking',
              price: 322000
            }
          ]
        }, {
          name: '호텔5',
          address: '두바이, 국제금융센터',
          star: 4,
          review: 2546,
          image: [
            'https://gotrip.s3.amazonaws.com/storage/go_trip/product/id/7/1/3/1/b/6/1/7131b61c225ecfb6a2ef65e57094449f/10002/9043f08df49faa258cb413d9e2154c5e.jpg'
          ],
          deeplink: [
            {
              agent: 'agoda',
              price: 321000
            },
            {
              agent: 'booking',
              price: 322000
            }
          ]
        }, {
          name: '호텔6',
          address: '두바이, 국제금융센터',
          star: 3,
          rating: '8.0',
          review: 2546,
          image: [
            'http://www.thebling.co.kr/wp-content/uploads/2017/08/budapest-1500x1039.png'
          ],
          deeplink: [
            {
              agent: 'agoda',
              price: 321000
            },
            {
              agent: 'booking',
              price: 322000
            }
          ]
        }, {
          name: '호텔7',
          address: '두바이, 국제금융센터',
          star: 3,
          rating: '9.0',
          review: 2546,
          image: [
            'http://www.socuripass.com/wp-content/uploads/2016/02/vie-budapest-onedaytour-6.jpg',
            'http://www.thebling.co.kr/wp-content/uploads/2017/08/budapest-1500x1039.png'
          ],
          deeplink: [
            {
              agent: 'agoda',
              price: 321000
            },
            {
              agent: 'booking',
              price: 322000
            }
          ]
        }]
        for (let x = 0; x < arr.length; x += 1) {
          this.hotelList.list.push(arr[x])
        }
        this.hotelList.param = this.param
        this.hotelList.searchTime = new Date().getTime()
      }, 1000)
    },
    getContStyle (index) {
      const styleObj = {}
      const gap = this.startOffsetY - this.moveOffsetY
      if (index === 'before') {
        if (this.hotelIndex === 0 || this.moveOffsetY === 0) {
          styleObj['opacity'] = 0
        } else if (this.startOffsetY < this.moveOffsetY) {
          const gapPercent = gap * -1 / this.listHeight
          styleObj['opacity'] = gapPercent
          const gapPercentBig = (gapPercent / 10) + 0.9
          styleObj['transform'] = `scale3d(${gapPercentBig},${gapPercentBig},${gapPercentBig})`
        } else {
          styleObj['opacity'] = 0
        }
      } else if (index === 'present') {
        if (this.moveOffsetY === 0) {
          styleObj['opacity'] = 1
        } else if (this.hotelIndex !== this.hotelList.list.length - 1 && this.startOffsetY > this.moveOffsetY) {
          const gapPercent = 1 - (gap / this.listHeight)
          styleObj['opacity'] = gapPercent
          const gapPercentBig = (gapPercent / 10) + 0.9
          styleObj['transform'] = `scale3d(${gapPercentBig},${gapPercentBig},${gapPercentBig})`
        } else if (this.hotelIndex !== 0 && this.startOffsetY < this.moveOffsetY) {
          styleObj['margin-top'] = `${(gap * -1)}px`
        } else {
          styleObj['opacity'] = 1
        }
      } else if (index === 'after') {
        if (this.moveOffsetY === 0) {
          styleObj['margin-top'] = `${this.listHeight}px`
        } else if (this.startOffsetY > this.moveOffsetY) {
          styleObj['margin-top'] = `${this.listHeight - gap}px`
        } else {
          styleObj['margin-top'] = `${this.listHeight}px`
        }
      }
      return styleObj
    },
    openPop (flag) {
      const objParam = this.$route.params
      objParam.popFlag = flag
      this.$router.push({
        params: objParam
      })
      this.popping = 'open'
      setTimeout(() => {
        this.popping = false
      }, 150)
    },
    closePop () {
      this.popping = 'close'
      setTimeout(() => {
        window.history.back()
      }, 150)
    },
    scrollDown (element, to, duration) {
      const start = parseInt(element.style.marginTop)
      const change = to - start
      const increment = 20
      let currentTime = 0
      const precentElement = this.$refs.present
      const animateScroll = () => {
        currentTime += increment
        var val = this.easeInOutQuad(currentTime, start, change, duration)
        element.style.marginTop = `${val}px`
        precentElement.style.opacity = val / this.listHeight
        const perBig = ((val / this.listHeight) * 0.1) + 0.9
        precentElement.style.transform = `scale3d(${perBig},${perBig},${perBig})`
        if (currentTime < duration) {
          setTimeout(animateScroll, increment)
        } else {
          this.startOffsetY = 0
          this.moveOffsetY = 0
          this.$emit('hotel-index-change', 1)
          this.duringScroll = false
        }
      }
      animateScroll()
    },
    scrollUp (element, to, duration) {
      const start = parseInt(element.style.marginTop)
      const change = to - start
      const increment = 20
      let currentTime = 0
      const precentElement = this.$refs.before
      const animateScroll = () => {
        currentTime += increment
        var val = this.easeInOutQuad(currentTime, start, change, duration)
        element.style.marginTop = `${val}px`
        precentElement.style.opacity = val / this.listHeight
        const perBig = ((val / this.listHeight) * 0.1) + 0.9
        precentElement.style.transform = `scale3d(${perBig},${perBig},${perBig})`
        if (currentTime < duration) {
          setTimeout(animateScroll, increment)
        } else {
          this.startOffsetY = 0
          this.moveOffsetY = 0
          this.$emit('hotel-index-change', -1)
          this.duringScroll = false
        }
      }
      animateScroll()
    },
    easeInOutQuad (t, b, c, d) {
      t /= d / 2
      if (t < 1) return c / 2 * t * t + b
      t--
      return -c / 2 * (t * (t - 2) - 1) + b
    }
  },
  mounted () {
    const vThis = this
    function findListHeight () {
      if (vThis.$refs.hotelList && vThis.$refs.hotelList.clientHeight > 0) {
        vThis.listHeight = vThis.$refs.hotelList.clientHeight
      } else {
        setTimeout(findListHeight, 100)
      }
    }
    if (this.$refs.hotelList && this.$refs.hotelList.clientHeight > 0) {
      this.listHeight = this.$refs.hotelList.clientHeight
    } else {
      setTimeout(findListHeight, 100)
    }
    this.getHotelList()
    console.log('mounted')
  },
  created () {
    const objParam = this.$route.params
    if (objParam.popFlag) {
      const path = this.$route.path
      const arrPath = path.split('/')
      arrPath.pop()
      this.$router.replace(arrPath.join('/'))
    }
  }
}
</script>

<style scoped>
  .listWrapper{
    background-color: #5C6E7C;
    height: 100%;
  }
  .hotelList{
    position: absolute;
    bottom: 0;
    left: 0;
    top: 70px;
    right: 0;
    overflow: hidden;
    user-select: none;
  }
  .hotelListCont{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: #846b55;
    height: 100%;
  }
  .searchPop{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,.3);
    z-index: 10;
  }
  .btnContainer{
    position: absolute;
    bottom: 16px;
    width: 240px;
    height: 46px;
    left: 50%;
    transform: translateX(-50%);
    background-color: #1A1A1C;
    border-radius: 23px;
    font-size: 0;
    z-index: 2;
  }
  .btnContainer > button{
    color: #FFF;
    height: 46px;
    padding: 0;
    font-size: 12px;
    padding-left: 12px;
    position: relative;
  }
  .btnContainer > button:nth-child(2) {
    width: 28%;
    position: relative;
  }
  .btnContainer > button:nth-child(1):after{
    border-left: 1px solid #4d4d4d;
    content: '';
    position: absolute;
    right: 0;
    height: 16px;
    top: 15px;
  }
  .btnContainer > button:nth-child(2):after{
    border-right: 1px solid #4d4d4d;
    content: '';
    position: absolute;
    right: 0;
    height: 16px;
    top: 15px;
  }
  .btnContainer > button:first-of-type, .btnContainer > button:last-of-type{
    width: 36%;
  }
  .btnContainer > button:before{
    width: 12px;
    height: 12px;
    background-size: cover;
    position: absolute;
    left: 12px;
    top: 17px;
    content: '';
  }
  .btnContainer > button:first-of-type:before{
    background-image: url(../../assets/filter.svg);
  }
  .btnContainer > button:nth-of-type(2):before{
    background-image: url(../../assets/sorting.svg);
  }
  .btnContainer > button:last-of-type:before{
    background-image: url(../../assets/map.svg);
  }
  .drop-shadow-up {
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0;
    height: 33px;
    opacity: 0;
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), #000000);
    z-index: 1;
    transition: opacity .5s ease;
    pointer-events: none;
  }
  .drop-shadow-up.show{
    opacity: .3;
  }
</style>



